@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Montserrat:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.montserrat {
  font-family: "Montserrat", sans-serif !important;
}

div .jumbotron {
  background: white;
}

.jumbotron h1 {
  font-family: "Amatic SC", cursive !important;
}

.modal-header {
  align-items: center !important;
}

.modal-title {
  line-height: 0.7 !important;
}

.card a.text-light:focus,
.card a.text-light:hover {
  text-decoration: none;
  color: #f8f9fa !important;
}

.artistInfo a.text-dark:focus,
.artistInfo a.text-dark:hover {
  text-decoration: none;
}

.pointer {
  cursor: pointer !important;
}

.artistInfo .nameLink {
  font-size: 0.8rem !important;
}

.zoom-in {
  cursor: zoom-in !important;
}

.zoom-out {
  cursor: zoom-out !important;
}

div .modal-xl {
  max-width: 85%;
}

#toTopButton {
  opacity: 0;
  transition: opacity 1s;
}

#toTopButton.fade {
  opacity: 1;
}

.modal .btn-outline-light:hover {
  color: #f8f9fa !important;
  background: rgba(201, 76, 76, 0) !important;
}

.size {
  max-height: 700px;
}

.fullsize {
  width: 100% !important;
  height: 100%;
}

@media (min-width: 1200px) {
  div .container {
    max-width: 1300px;
  }
}

@media (max-width: 992px) {
  .jumbotron h1 {
    font-size: 4rem;
  }
  .jumbotron p {
    font-size: 1.2rem;
  }
}

@media (min-width: 768px) {
  .modal-arrow-left {
    display: block;
    position: fixed;
    top: 45%;
    left: 2%;
  }
  .modal-arrow-right {
    display: block;
    position: fixed;
    top: 45%;
    right: 2%;
  }
}

@media (max-width: 768px) {
  .modal-arrow-left {
    display: none;
  }
  .modal-arrow-right {
    display: none;
  }
  .hide-md {
    display: none !important;
  }
  .width-md-100 {
    max-width: 100% !important;
    flex: 0 0 100% !important;
    text-align: center;
  }
  #mainModal {
    max-width: 100% !important;
    margin: 0;
  }

  .modal-body .overlay {
    display: flex !important;
  }
}

@media (max-width: 930px) {
  .btn-md-sm {
    padding: 0.25rem 0.5rem !important;
    font-size: 0.875rem !important;
    line-height: 1.5 !important;
    border-radius: 0.2rem !important;
  }
}

.masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  margin-left: -15px; /* gutter size offset */
  width: auto;
}
.masonry-grid_column {
  padding-left: 15px; /* gutter size */
  background-clip: padding-box;
  overflow: hidden;
}

/* Style your items */
.masonry-grid_column > div {
  /* change div to reference your elements you put in <Masonry> */
  background-color: grey;
  margin-bottom: 15px !important;
  width: 100%;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  color: #fff;
}
.width-100 {
  width: 100%;
}

.card:hover .overlay {
  display: flex;
  background: rgba(0, 0, 0, 0.12);
}

.wrap {
  height: 100%;
  width: 100%;
  text-align: center;
}

.wrap img {
  height: 50px;
}

.bounce {
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation: bounce 3.6s ease infinite;
          animation: bounce 3.6s ease infinite;
  transform-origin: 50% 50%;
}

@-webkit-keyframes bounce {
  0% {
    transform: translateY(0);
  }
  5.55556% {
    transform: translateY(0);
  }
  11.11111% {
    transform: translateY(0);
  }
  22.22222% {
    transform: translateY(-15px);
  }
  27.77778% {
    transform: translateY(0);
  }
  33.33333% {
    transform: translateY(-15px);
  }
  44.44444% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  5.55556% {
    transform: translateY(0);
  }
  11.11111% {
    transform: translateY(0);
  }
  22.22222% {
    transform: translateY(-15px);
  }
  27.77778% {
    transform: translateY(0);
  }
  33.33333% {
    transform: translateY(-15px);
  }
  44.44444% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
}

